* {
  outline: none; 
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #002350;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #01152E, #002350);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(left, #01152E, #002350);
  background: linear-gradient(to right, #01152E, #002350); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f11e; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 225, 0, 0.733); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(216, 191, 0, 0.788); 
}
@font-face {
  font-family: 'starwars';
  src: url(/starwars_api/static/media/STJEDISE.a43e30ec.TTF);
}

h1 {
  font-family: 'starwars';
  color: rgb(255, 225, 0);
}

.card {
  background-color: rgba(43, 43, 43, 0.466);
  color: #eee;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
